"use client"

import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { MapPin, Users, TrendingUp, ChevronLeft, ChevronRight } from "lucide-react"
import Image from "next/image"
import { useState, useEffect } from "react"

const SLIDESHOW_IMAGES = [
  {
    src: "https://live.staticflickr.com/65535/54635610770_b2cbf396d6_c.jpg",
    alt: "Palestra na Python Norte",
  },
  {
    src: "https://live.staticflickr.com/65535/54032804453_cfef802556_c.jpg",
    alt: "Networking entre desenvolvedores",
  },
  {
    src: "https://live.staticflickr.com/65535/53231412486_a64dbe8281_c.jpg",
    alt: "Workshop de programação Python",
  }
]

export function Hero() {
  const [currentSlide, setCurrentSlide] = useState(0)

  useEffect(() => {
    const timer = setInterval(() => {
      setCurrentSlide((prev) => (prev + 1) % SLIDESHOW_IMAGES.length)
    }, 5000)
    return () => clearInterval(timer)
  }, [])

  const nextSlide = () => {
    setCurrentSlide((prev) => (prev + 1) % SLIDESHOW_IMAGES.length)
  }

  const prevSlide = () => {
    setCurrentSlide((prev) => (prev - 1 + SLIDESHOW_IMAGES.length) % SLIDESHOW_IMAGES.length)
  }

  return (
    <section className="relative min-h-screen w-full flex items-center justify-center overflow-hidden">
      <div className="absolute inset-0 w-full h-full">
        {SLIDESHOW_IMAGES.map((image, index) => (
          <div
            key={index}
            className={`absolute inset-0 transition-opacity duration-1000 ${
              index === currentSlide ? "opacity-100" : "opacity-0"
            }`}
          >
            <Image
              src={image.src || "/placeholder.svg"}
              alt={image.alt}
              fill
              className="object-cover"
              priority={index === 0}
            />
          </div>
        ))}

        <div className="absolute inset-0 bg-gradient-to-b from-black/60 via-black/50 to-black/70" />
      </div>

      <button
        onClick={prevSlide}
        className="absolute left-4 md:left-8 top-1/2 -translate-y-1/2 bg-background/30 hover:bg-background/50 backdrop-blur-sm text-white p-3 rounded-full transition-all shadow-lg z-20 border border-white/20"
        aria-label="Foto anterior"
      >
        <ChevronLeft className="w-6 h-6" />
      </button>
      <button
        onClick={nextSlide}
        className="absolute right-4 md:right-8 top-1/2 -translate-y-1/2 bg-background/30 hover:bg-background/50 backdrop-blur-sm text-white p-3 rounded-full transition-all shadow-lg z-20 border border-white/20"
        aria-label="Próxima foto"
      >
        <ChevronRight className="w-6 h-6" />
      </button>

      <div className="absolute bottom-8 left-1/2 -translate-x-1/2 flex gap-2 z-20">
        {SLIDESHOW_IMAGES.map((_, index) => (
          <button
            key={index}
            onClick={() => setCurrentSlide(index)}
            className={`h-2 rounded-full transition-all ${index === currentSlide ? "bg-white w-8" : "bg-white/40 w-2"}`}
            aria-label={`Ir para foto ${index + 1}`}
          />
        ))}
      </div>

      <div className="container mx-auto px-4 relative z-10">
        <div className="max-w-4xl mx-auto text-center">
          {/* Badges */}
          <div className="flex flex-wrap items-center justify-center gap-3 mb-8 animate-fade-in">
            <Badge
              variant="secondary"
              className="px-4 py-2 text-sm font-semibold bg-primary/90 backdrop-blur-sm text-primary-foreground border border-white/20 shadow-lg"
            >
              <MapPin className="w-4 h-4 mr-2" />
              10 anos (2015–2025)
            </Badge>
            <Badge
              variant="secondary"
              className="px-4 py-2 text-sm font-semibold bg-secondary/90 backdrop-blur-sm text-secondary-foreground border border-white/20 shadow-lg"
            >
              <Users className="w-4 h-4 mr-2" />
              +2.000 participantes
            </Badge>
            <Badge
              variant="secondary"
              className="px-4 py-2 text-sm font-semibold bg-accent/90 backdrop-blur-sm text-accent-foreground border border-white/20 shadow-lg"
            >
              <TrendingUp className="w-4 h-4 mr-2" />
              +200k de alcance
            </Badge>
          </div>

          {/* Main Title */}
          <h1 className="text-5xl md:text-7xl lg:text-8xl font-bold mb-6 text-balance leading-tight text-white drop-shadow-2xl">
            Python Norte
          </h1>

          {/* Subtitle */}
          <p className="text-lg md:text-xl text-white/95 mb-10 max-w-3xl mx-auto text-pretty drop-shadow-md backdrop-blur-sm bg-black/20 py-4 px-6 rounded-lg border border-white/10">
            Desde 2015: conectando desenvolvedores, compartilhando conhecimento e celebrando a comunidade Python
            Nortista.
            <span className="font-semibold text-white"> Feito pela comunidade e para a comunidade</span>
          </p>

          {/* CTAs */}
          <div className="flex flex-col sm:flex-row items-center justify-center gap-4">
            <Button
              asChild
              size="lg"
              className="bg-accent hover:bg-accent/90 text-accent-foreground text-lg px-8 py-6 shadow-2xl border-2 border-white/20"
            >
              <a href="#contato">Ingressos Python Norte 2026</a>
            </Button>
            <Button
              asChild
              size="lg"
              variant="outline"
              className="text-lg px-8 py-6 border-2 bg-white/10 hover:bg-white/20 backdrop-blur-sm text-white shadow-2xl border-white/30"
            >
              <a href="#edicoes">Ver edições</a>
            </Button>
          </div>
        </div>
      </div>
    </section>
  )
}
